@import "../../variables.modules";
@import url('https://fonts.googleapis.com/css?family=Roboto+Flex&display=swap');

.light {
  background-color: $white;
  color: $dark;
  transition: all 700ms;  // This is the same as the transition in src\Components\BaseLayout.module.scss 
} 
// Currently this is affecting all the other CSS, so make sure you adjust it so that it only works for the case studies.


.alignImages{
  display: flex;
  margin: auto;
  @media only screen and (max-width: 425px) {
    display: none;
  }
}

.alignLargeImages{
  display: flex;
  margin: auto;
  @media only screen and (max-width: 768px) {
    display: initial;
  }
}



body{
  font-weight: lighter;
}

.figma{
  margin: auto;
  border-style: none;
  height: 100%;
  display: block;
  padding-left: 20em;
  align-items: center;
  padding-bottom: 2rem;
}

.terminal {
  width: 80%;
  margin: 2rem auto 4rem auto;
  border-radius: .5rem;

  @media only screen and (min-width: 940px) {
    width: 50%;
    margin: 3rem auto 6rem auto;
  }
}

.topRow {
  background-color: #505050;
  color: white;
  padding: 0.5rem;
  border-radius: 0.5rem 0.5rem 0 0;
  font-size: 1rem;

  @media only screen and (min-width: 940px) {
    font-size: 1.3rem;
  }

  i {
    padding-left: 0.5rem;
  }
}

.red {
  color: #FF6057;
}

.amber {
  color: #FFBD2E;
}

.green {
  color: #27C93F;
}

.window {
  padding: 1rem 2rem;
  border-radius: 0 0 .5rem .5rem;
  background-color: rgba(10, 4, 17, 0.596);
  color: $dark;

  @media only screen and (min-width: 940px) {
    padding: 2rem 3rem;
  }
  p, li {
    font-size: 3vw;
    font-family: 'Courier New', Courier, monospace;
    margin: 1.3rem 0;

    @media only screen and (min-width: 940px) {
      font-size: 2rem;
      margin: 2rem 0;
    }
  }
}


.emoji {
  margin-right: 1rem;
}

.gif{
  padding-left: 29rem;
}


.CaseStudy{
  
  @keyframes fadeInRight {
    from {
      opacity: 0;
      transform: translateX(-300px);
    }
    to {
      opacity: 1;
    }
  }
  
  h1{
    text-align: left;
    margin-left: 5%;
    font-size: 4vh;
    animation: fadeInRight 1s ease-in-out;
    font-family: 'Roboto', sans-serif;
    border-bottom:1px solid rgb(255, 255, 255);
    width: 80%; 
    padding-top: 5%;
  }

  
  
  img{
    padding-top: 4%;
    width: 50%;
    margin: auto;
    display: block;
    animation: fadeInRight 1s ease-in-out;
    @keyframes fadeInRight {
      from {
        opacity: 0;
        transform: translateX(-300px);
      }
      to {
        opacity: 1;
      }
    }
  }

  h2{
    margin-left: 10%;
    margin-top: 2%;
    font-size: 3vh;
    font-family: 'Roboto', sans-serif;
    width: 70%;
    padding-bottom:2vh;
  }

  p{
    margin-left: 12%;
    margin-top: 1%;
    font-size: 2.5vh;
    font-family: 'Roboto', sans-serif;
    width: 70%;
  }

  iframe{
    display: block;
    height: 50rem;
    margin:auto;
    width: 80%;
    margin-top: 2%;

    @media only screen and (min-width: 768px) and (max-width: 1023px){
      display:none;
    }
    
    @media only screen and (min-width: 480px) and (max-width: 767px){
      display:none;
    }
    
    @media only screen and (min-width: 0px) and (max-width: 479px){
      display:none;
    }
  }

}

